import React from "react";
import { Routes, Route, Navigate } from "react-router-dom";
import Login from "./componentes/login/Login"; // Asegúrate de importar tu componente Login
import Dashboard from "./componentes/dashboard/Dashboard"; // Asegúrate de crear tu componente Dashboard

// Componente para proteger las rutas privadas
const PrivateRoute = ({ children }) => {
  const token = localStorage.getItem("token"); // Verifica si el usuario tiene el token guardado
  return token ? children : <Navigate to="/" />; // Si tiene el token, muestra el componente. Si no, redirige a Login.
};

function App() {
  return (
    <Routes>
      {/* Ruta para iniciar sesión */}
      <Route path="/" element={<Login />} />

      {/* Ruta protegida para el dashboard */}
      <Route
        path="/dashboard"
        element={
          <PrivateRoute>
            <Dashboard />
          </PrivateRoute>
        }
      />
    </Routes>
  );
}

export default App;
