import React, { useState } from "react";
import { Box, Button, FormControl, FormLabel, Input, Heading, VStack, Image, InputGroup, InputRightElement, IconButton } from "@chakra-ui/react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import Swal from "sweetalert2";
import { ViewIcon, ViewOffIcon } from "@chakra-ui/icons"; // Chakra UI icons
import logo from '../../img/logo.png';

const Login = () => {
  const [loading, setLoading] = useState(false);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false); // Estado para manejar la visibilidad de la contraseña
  const navigate = useNavigate();

  // Función para iniciar sesión
  const onFinish = async (e) => {
    e.preventDefault();
    setLoading(true);

    try {
        console.log(`${process.env.REACT_APP_backend}/iniciar`);
      const response = await axios.post(`${process.env.REACT_APP_backend}/iniciar`, { email, password });
      const { token } = response.data;

      localStorage.setItem("token", token);

      Swal.fire({
        icon: "success",
        title: "Inicio de sesión exitoso",
        text: "Bienvenido de nuevo!",
        confirmButtonText: "Ok",
      });

      navigate('/dashboard');
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Error",
        text: "Credenciales incorrectas. Por favor, intenta de nuevo.",
        confirmButtonText: "Ok",
      });
    } finally {
      setLoading(false);
    }
  };

  const handleCancel = () => {
    navigate("/");
  };

  return (
    <Box
      w="100vw"
      h="100vh"
      display="flex"
      justifyContent="center"
      alignItems="center"
      bg="gray.100"
    >
      <Box
        w={{ base: "90%", md: "400px" }}
        p="8"
        bg="white"
        borderRadius="lg"
        boxShadow="lg"
      >
        {/* Logo */}
        <Box textAlign="center" mb="6">
          <Image src={logo} alt="Logo" maxW="200px" mx="auto" />
        </Box>

        {/* Título */}
        <Heading as="h2" size="lg" textAlign="center" mb="6">
          Iniciar Sesión
        </Heading>

        {/* Formulario */}
        <form onSubmit={onFinish}>
          <VStack spacing="4">
            <FormControl id="email" isRequired>
              <FormLabel>Correo electrónico</FormLabel>
              <Input
                type="email"
                placeholder="Ingrese su correo"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
            </FormControl>

            <FormControl id="password" isRequired>
              <FormLabel>Contraseña</FormLabel>
              <InputGroup>
                <Input
                  type={showPassword ? "text" : "password"} // Cambia entre "text" y "password"
                  placeholder="Ingrese su contraseña"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
                <InputRightElement>
                  <IconButton
                    aria-label={showPassword ? "Ocultar contraseña" : "Mostrar contraseña"}
                    icon={showPassword ? <ViewOffIcon /> : <ViewIcon />}
                    onClick={() => setShowPassword(!showPassword)} // Alterna el estado de visibilidad
                    variant="ghost"
                    size="sm"
                    tabIndex="-1"
                  />
                </InputRightElement>
              </InputGroup>
            </FormControl>

            {/* Botones */}
            <Button
              colorScheme="blue"
              type="submit"
              isLoading={loading}
              loadingText="Iniciando sesión..."
              width="full"
            >
              Iniciar Sesión
            </Button>
            <Button
              variant="outline"
              onClick={handleCancel}
              width="full"
            >
              Cancelar
            </Button>
          </VStack>
        </form>
      </Box>
    </Box>
  );
};

export default Login;
